body{
  background: #eee;
}
#root{
  display: flex;
  justify-content: center;
  min-height: 100vh;
}

.App{
    border: 1px solid #ddd;
    color: #333;
    background: #fff;
    margin: 2em;
    padding: 0 1em;
    min-height: 20em;
    min-width: 32em;
    box-shadow: 0 0 1em hsla(0,0%,0%,0.15);
}
.App h1{
    text-align: center;
    color: rgba(175, 47, 47, 0.15);
}
.inputWrapper input{
    margin-left: 20%;
    margin-right: 20%;
    padding: 10px;
    border: 1px solid white;
    box-shadow: #ddd;
}
.App input:focus{
    outline: none;
}
.App input::-webkit-input-placeholder{
    color: #ddd;
}
.App input::-ms-input-placeholder{
    color: #ddd;
}
.App input::placeholder{
    color: #ddd;
}
.itemWrapper{
    width: 60%;
    margin: 0 auto;
}
.itemWrapper::after{
    content: '';
    display: block;
    clear: both;
}
.itemWrapper input{
    width: 14px;
    height: 14px;
    margin: 4px;
    float: left;
}
.itemWrapper span{
    line-height:22px;
    float: left;
    color: #666;
}
.itemWrapper button{
    float: right;
    background:#fff;
    color: #ddd;
    border: 0;
}
.itemWrapper button:focus{
    outline: none;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
*::after,*::before{
    box-sizing: border-box;
}
ul,ol,dl{
    list-style: none;
}
a{
    color:inherit;
}

